import React from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter, Route, Switch, Redirect } from "react-router-dom";
import './index.css';
import * as serviceWorker from './serviceWorker';
import { createBrowserHistory } from "history";

import Login from './app/screens/SignIn/Main';
import App from './App';
import Register from './app/screens/Register/Main';
import Profile from './app/screens/UserDashboard/Main';
import Blog from './app/screens/Blog/Main'
import AppHome from './app/screens/Home/Main';
import AdminDashboard from './admin/index'
import 'bootstrap/dist/css/bootstrap.css';
const hist = createBrowserHistory();

ReactDOM.render(
  <React.StrictMode>
  <div style={{minWidth: '100%', minHeight: '100%', padding: 0, margin: 0, display: 'flex'}}>
      <BrowserRouter history={hist}>
        <Switch>
            <Route path="/" exact render={props => <App {...props} />} />
                <Route path="/login-page" exact render={props => <Login {...props} />} />
                  <Route
                    path="/profile-page"
                    exact
                    render={props => <Profile {...props} />}
                  />

                    <Route
                      path="/signup"
                      exact
                      render={props => <Register {...props} />}
                    />

                      <Route
                      path="/blog"
                      exact
                      render={props => <Blog {...props} />}
                     />
                <Route path="/test" exact render={props => <AppHome {...props} />} />

                <Route path="/admin" exact render={props => <AdminDashboard {...props} />} />
                    <Redirect from='/admin/dashboard' to="/admin" />
                      <Redirect to="/" />
        </Switch>
    </BrowserRouter>
    </div>,
  </React.StrictMode>,
  document.getElementById('root')
);


// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
