import React from 'react';
import {IoMdPerson, IoMdPhonePortrait} from 'react-icons/io';
import { MdEmail, MdLocationOn } from "react-icons/md";
import {colors, dimensions} from '../styles/base';
import renderIf from './conditionalRender';

const componentName = ({title, onClick, background, bordercolor, color, iconName, placeholder}) => (
  <div style={container}>
    <div style={icon}>
      {renderIf(
        iconName === 'IoMdPerson',
        <IoMdPerson color={'#ADB5BD'}  size={16} />,
      )}
      {renderIf(
        iconName === 'MdEmail',
        <MdEmail color={'#ADB5BD'}  size={16} />,
      )}
      {renderIf(
        iconName === 'IoMdPhonePortrait',
        <IoMdPhonePortrait color={'#ADB5BD'} size={16}  />
      )}
      {renderIf(
        iconName === 'MdLocationOn',
        <MdLocationOn color={'#ADB5BD'} size={16}  />
        )}
    </div>
    {renderIf(
      iconName === 'IoMdPerson',
      <input placeholder= {placeholder} type='text'  style={form} />,
    )}
    {renderIf(
      iconName === 'MdEmail',
      <input placeholder= {placeholder} type='text'  style={form} />,
    )}
    {renderIf(
      iconName === 'IoMdPhonePortrait',
      <input placeholder= {placeholder} type='number'  style={form} />,
    )}
    {renderIf(
      iconName === 'MdLocationOn',
      <input placeholder= {placeholder} type='text'  style={form} />,
    )}
  </div>
);

const container = {
  display: 'flex',
  flexDirection: 'row',
  background: '#fff',
  width: '40%',
  borderRadius: 4,
  boxShadow: '1px 2px 1px #DEE1E7',
}

const icon = {
  padding: 0,
  margin: 0,
  display: 'flex',
  width: '20%',
  justifyContent: 'center',
  alignItems: 'center',
  alignContent: 'center',
}
const form = {
  border: '0px',
  width: '80%',
  margin: 0,
  padding: 0
};
export default componentName;
