import React from 'react';
import renderIf from './conditionalRender';
import countryList from 'react-select-country-list';
import Select from 'react-select';

export default class Modal extends React.Component {
    constructor(props) {
      super(props)
   
      this.options = countryList().getData()
   
      this.state = {
        options: this.options,
        value: null,
      }
    }
   
    changeHandler = value => {
      this.setState({ value })
    }
   
    componentDidMount() {
    }
    render() {
      return (
        <>
            <div style={container}>
                <Select
                options={this.state.options}
                value={this.state.value}
                onChange={this.changeHandler}
                />
            </div>
        </>
);
}
}
const container = {
    display: 'block',
    background: '#fff',
    width: '40%',
    borderRadius: 4,
    boxShadow: '1px 2px 1px #DEE1E7',
  }


const customStyles = {
    height: '100%'
  }